.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;
    height: 1080px;

    grid-template-areas: 'h h' 'n c';

    grid-template-rows: 100px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: c;
    background-color: aquamarine;

}

